import React, { FC, ReactNode } from 'react';
import moment from 'moment';
import { navigate } from 'gatsby';
import { Box, PseudoBox, Text, Stack, Flex } from '@chakra-ui/core';
import { Calendar, DollarSign, Tag, UserPlus } from 'react-feather';

interface CardProps {
  pageLink: string;
  title: string;
  fundingBody: string;
  fundingAmount: string;
  investigators: string;
  projectStart: string;
  projectEnd: string;
}

export const Card: FC<CardProps> = ({
  pageLink,
  title,
  fundingBody,
  fundingAmount,
  investigators,
  projectStart,
  projectEnd,
}) => {
  const iconSize = '24px';

  return (
    <PseudoBox
      p={8}
      borderWidth={1}
      borderColor={'jet-black.300'}
      borderRadius={'1rem'}
      boxShadow={
        '0 1px 3px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0.06)'
      }
      color={'jet-black.500'}
      cursor={'pointer'}
      _hover={{ backgroundColor: 'jet-black.100' }}
      onClick={() => navigate(pageLink)}
    >
      <Text
        fontSize={['lg', 'xl', '2xl']}
        fontWeight={'semibold'}
        lineHeight={['1.25rem', '1.5rem', '1.75rem']}
      >
        {title}
      </Text>

      <Stack mt={4} mx={4}>
        {fundingBody && (
          <Item
            icon={<Tag size={iconSize} />}
            component={<Text>{fundingBody}</Text>}
          />
        )}
        {fundingAmount && (
          <Item
            icon={<DollarSign size={iconSize} />}
            component={<Text>{fundingAmount}</Text>}
          />
        )}
        {investigators && (
          <Item
            icon={<UserPlus size={iconSize} />}
            component={<Text>{investigators}</Text>}
          />
        )}
        {projectStart && (
          <Item
            icon={<Calendar size={iconSize} />}
            component={
              <Box>
                <Text display={'inline-block'} fontWeight={'semibold'}>
                  {moment(projectStart).format('MMMM YYYY')}
                </Text>
                {projectEnd && (
                  <>
                    <Text display={'inline-block'} mx={1}>
                      till
                    </Text>
                    <Text display={'inline-block'} fontWeight={'semibold'}>
                      {moment(projectEnd).format('MMMM YYYY')}
                    </Text>
                  </>
                )}
              </Box>
            }
          />
        )}
      </Stack>
    </PseudoBox>
  );
};

const Item: FC<{ icon: ReactNode; component: ReactNode }> = ({
  icon,
  component,
}) => (
  <Flex alignItems={'center'} mt={4}>
    <Box color={'emerald-green.500'} mr={[4, 6]}>
      {icon}
    </Box>
    <Box>{component}</Box>
  </Flex>
);
